.navigation-container {
  width: 100%;
  padding: 10px;
  background-color: #fff;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.toggle-btn {
  display: none;
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1030;
  padding: 0.5rem;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.toggle-btn.menu-open {
  transform: rotate(180deg);
  background-color: #fff !important;
  color: #209cff !important;
}

.toggle-icon {
  transition: transform 0.3s ease;
}

.menu-open .toggle-icon {
  transform: rotate(180deg);
}

.navigation-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  transform: translateX(-100%);
  transition: transform 0.5s ease;
  z-index: 1040;
}

.navigation-menu.show {
  transform: translateX(0);
}

.nav-items-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: flex-start;
}

.nav-item {
  cursor: pointer;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  opacity: 0;
  animation: fadeInUp 0.5s forwards;
  width: 100%;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-5px);
}

.nav-item.active {
  background: rgb(255, 255, 255);
  transform: scale(1.1);
}

.nav-link-custom {
  color: rgb(6, 165, 239);
  text-decoration: none;
  width: 100%;
  display: block;
}

.icon-container {
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
}

.nav-label {
  font-size: 0.875rem;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.3s ease;
}

.nav-label.show {
  opacity: 1;
  transform: translateY(0);
}

@media (max-width: 767px) {
  .toggle-btn {
    display: flex;
  }

  .nav-item {
    opacity: 0;
    animation: fadeInUp 0.5s ease forwards;
    transform: translateY(20px);
  }

  .navigation-menu.show .nav-item:nth-child(1) { animation-delay: 0.1s; }
  .navigation-menu.show .nav-item:nth-child(2) { animation-delay: 0.2s; }
  .navigation-menu.show .nav-item:nth-child(3) { animation-delay: 0.3s; }
  .navigation-menu.show .nav-item:nth-child(4) { animation-delay: 0.4s; }
  .navigation-menu.show .nav-item:nth-child(5) { animation-delay: 0.5s; }
  .navigation-menu.show .nav-item:nth-child(6) { animation-delay: 0.6s; }
  .navigation-menu.show .nav-item:nth-child(7) { animation-delay: 0.7s; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (min-width: 768px) {
  .navigation-menu {
    position: relative;
    width: auto;
    max-width: none;
  }
}
